
.other-courses{
    .previous-details{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 20px;
        margin-left: 20px;

        .preimage{
            img{
                width:100px;
                height: 100px;
                border-radius: 15px;

            }
        }

        .other-course-details{
            margin: 0 15px;
            padding: 0;

            h2{
                margin: 0;
                padding: 0;
                margin-top: 10px;
            }
            p{
                margin: 0;
                padding: 0;
            }

            .sticker{
                background-color: #4C5D34;
                padding: 2px 20px;
                color: white;
                font-size: 12px;
                text-align: center;
            }
        }
    }
}