body {
    background-color: #2B411C;
}

/* Hide the carousel on large screens */


  
.root-component {
    display: flex;
    flex-direction: column;
    // justify-content: center;
    align-items: center;
    // min-height: 100vh; /* Full viewport height */
    position: relative; /* Ensure footer stays at the bottom */
}

.root-component-login {
    display: flex;
    flex-direction: column;
    // justify-content: center;
    align-items: center;
    height: 100%;
    // min-height: 100vh; /* Full viewport height */
    position: relative; /* Ensure footer stays at the bottom */
}

.w100{
    width: 100%;
}

.h100{
    height: 100%;
}

.image.center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1; /* Takes available space */
    width: 100%; /* Ensure it takes the full width available */
}

.image.center img {
    min-width: 20%; /* Makes sure the image scales down on smaller screens */
    height: auto; /* Maintains the aspect ratio */
}

.footer-img {
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    // width: 100px; /* Set a default width */
    max-width: 100%; /* Ensures it doesn't overflow the viewport */
}


.loginscreen{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    margin: auto;
}

.custom-form-group {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.custom-form-group input {
    width: 50%;
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #f28c32;
    background-color: transparent;
    border-radius: 5px;
    color: #fff;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);



}

.custom-form-group button {
    width: 30%;
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #000;
    border-radius: 5px;
    background-color: #4C5D34;
    color: #fff;
    cursor: pointer;
    // shadow
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.title {
    color: #fff;
    font-size: 24px;
    margin-bottom: 20px;
}

.carousel-banners {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0 10px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);  
    overflow: hidden;
}

.dashboard-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    width: 100%;
}

.hi-name {
    color: #fff;
    font-size: 24px;
    margin-bottom: 20px;
    text-align: left;
    margin: 10px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

// .hi-name span {
//     margin: 0px;
//     font-size: rem;
//     color: orange;
// }

.hi-name p {
    margin: 0px;
    color: #fff;
}

.center{
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    width: 100%;
}

.enrolled-course {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 95%;
    margin: auto;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);  
    overflow: hidden;
    background-color: #4C5D34;
    margin-bottom: 10px;
}

.course-link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 98%;
    margin: 0 10px;
    // margin: auto;
    // border-radius: 10px;
    // box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);  
    // overflow: hidden;
    // background-color: #4C5D34;
}

.course-thumbnail {
    width: 100px;
    height: 100px;
    margin: 10px 0;
    border-radius: 10px;
    
    align-items: flex-start;
}


.course-details {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: left;
    width: 100%;
    margin: 10px;
}
.course-description{
    margin-bottom: 20px;
    text-align: left;
    padding: 0;
    margin: 10px;
    color: #cfcfcf;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

}

.course-title {
    color: #fff;
    font-size: 24px;
    margin-bottom: 20px;
    text-align: left;
    padding: 0;
    margin: 10px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}


.subject-list {
    display: flex;
    justify-content: space-evenly;
    align-items: left;
    width: 100%;
    flex-wrap: wrap;
    margin: auto;
    border-radius: 10px;
}

.subject-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width:180px;
    margin: 10px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);  
    overflow: hidden;
    background-color: #4C5D34;
    border-radius: 10px;
}

.subject-image {
    width: 150px;
    height: 150px;
    margin: 10px;
    align-items: flex-start;
    border-radius: 05px;
    overflow: hidden;
    background: #838383;
}

.subject-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.subject-title{
    color: #fff;
    font-size: 14px;
    width: 170px;
    margin: auto;
    margin-bottom: 20px;
    text-align: center;
    text-wrap: wrap;
    padding: 0;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}


/* NotFound.css */

.not-found-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
}

.not-found-image {
    max-width: 300px; /* Adjust the width as needed */
    width: 100%;
    height: auto;
    filter: invert(1);
    margin-bottom: 20px;
}

.not-found-title {
    font-size: 3rem;
    color: #ffffff;
    margin-bottom: 10px;
}

.not-found-text {
    font-size: 1.5rem;
    color: #e6e6e6;
}

.top-section{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
}

.top-section-header {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0 10px;

}
.top-section-header h1{
    color: #fff;
    font-size: 24px;
    margin-bottom: 20px;
    text-align: center;
    margin: 10px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

}
.top-section-content {
    width: 100%;
    margin: auto;

  }

.top-section-content ul{
  display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0;

}

.top-section-content li {
    list-style-type: none;
    display: inline-block; /* Optional: to keep li elements inline */
    position: relative; /* Needed for positioning the ::after pseudo-element */
    color: #e1e1e1;
    margin: 0;
    font-size: 14px;
}

.top-section-content li:not(:last-child)::after {
    content: "|"; /* Add the character " |" */
    padding-left: 10px; /* Adjust the padding as needed */
    padding-right: 10px; /* Adjust the padding as needed */
}


